exports.push([module.id, "\n#resetPassword[data-v-2b64f060] {\n  max-width: 50%;\n  margin: 0 auto;\n}\n.title[data-v-2b64f060] {\n    width: 50%;\n    margin: 0 auto;\n    text-align: center;\n}\n.content[data-v-2b64f060] {\n    font-size: 16px;\n}\n.list[data-v-2b64f060] {\n  font-size: 13px;\n}\n\n", "", {"version":3,"sources":["/home/pool/IdeaProjects/arise/src/views/src/views/resetPassword.vue"],"names":[],"mappings":";AAyJA;EACA,eAAA;EACA,eAAA;CACA;AACA;IACA,WAAA;IACA,eAAA;IACA,mBAAA;CACA;AACA;IACA,gBAAA;CACA;AACA;EACA,gBAAA;CACA","file":"resetPassword.vue","sourcesContent":["<template>\n  <div id=\"resetPassword\">\n    <el-steps\n      :active=\"2\">\n      <el-step \n        :title=\"$t('reset.steps.title.1')\" \n        :description=\"$t('reset.steps.description.1')\"/>\n      <el-step \n        :title=\"$t('reset.steps.title.2')\" \n        :description=\"$t('reset.steps.description.2')\"/>\n      <el-step \n        :title=\"$t('reset.steps.title.3')\" \n        :description=\"$t('reset.steps.description.3')\"/>\n    </el-steps>\n    <el-card class=\"box-card\">\n      <div \n        slot=\"header\" \n        class=\"title\">\n        <font-awesome-icon \n          icon=\"lock-open\" \n          size=\"4x\"/>\n        <h2>{{ $t(\"reset.Password.title\") }}</h2>\n      </div>\n      <div class=\"description\">\n        {{ $t(\"reset.Password.description\") }}\n        <div class=\"list\">\n          <dl>\n            <dt>{{ $t('reset.Password.thingstoinclude.title') }}</dt>\n            <dd>{{ $t('reset.Password.thingstoinclude.1') }}</dd>\n            <dd>{{ $t('reset.Password.thingstoinclude.2') }}</dd>\n            <dt>{{ $t('reset.Password.thingstoavoid.title') }}</dt>\n            <dd>{{ $t('reset.Password.thingstoavoid.1') }}</dd>\n            <dd>{{ $t('reset.Password.thingstoavoid.2') }}</dd>\n            <dd>{{ $t('reset.Password.thingstoavoid.3') }}</dd>\n          </dl>\n        </div>\n      </div>\n      <el-form\n        :model=\"resetFormPassword\"\n        :rules=\"rules\"\n        status-icon>\n        <el-form-item\n          :label=\"$t('reset.label.password')\"\n          prop=\"password\">\n          <div id=\"pw\">\n            <password\n              v-model=\"resetFormPassword.password\"\n              :toggle=\"true\"\n              class=\"input\"\n              default-class=\"el-input__inner\"\n              @feedback=\"showFeedback\"/>\n          </div>\n        </el-form-item>\n        <el-form-item\n          :label=\"$t('reset.label.checkPassword')\"\n          prop=\"checkPass\">\n          <el-input\n            v-model=\"resetFormPassword.checkPass\"\n            type=\"password\"/>\n        </el-form-item>\n      </el-form>\n      <el-button\n        type=\"primary\"\n        icon=\"el-icon-arrow-right\"\n        @click.prevent=\"submitForm\">{{ $t('options.resetIt') }}</el-button>\n    </el-card>\n  </div>\n</template>\n\n<script>\n  import Vue from 'vue'\n  import Password from 'vue-password-strength-meter';\n  import axios from 'axios'\n  import VueAxios from 'vue-axios'\n  import {\n    Button,\n    Card,\n    Form,\n    FormItem,\n    Step,\n    Steps,\n    Input\n  } from 'element-ui'\n\n\n  Vue.use(VueAxios, axios);\n  Vue.use(Button);\n  Vue.use(Card);\n  Vue.use(Form);\n  Vue.use(FormItem);\n  Vue.use(Step);\n  Vue.use(Steps);\n  Vue.use(Input);\n\n  export default {\n        name: \"ResetPassword\",\n        components: {Password},\n\n        data() {\n          var checkPass = (rule, value, callback) => {\n            if (value === '') {\n              callback(new Error('Please input the password again'));\n            } else if (value !== this.signUpForm.password) {\n              callback(new Error('Two inputs don\\'t match!'));\n            } else {\n              callback();\n            }\n          };\n\n            return {\n\n              resetFormPassword: {\n                  password: '',\n                  checkPass: ''\n                },\n\n                rules: {\n                  password: [\n                    { required: true, trigger: 'blur'}\n                  ],\n                  checkPass: [\n                    { required: true, validator: checkPass, message: this.$t('validationError.checkPass'), trigger: 'blur' }\n                  ]\n              }\n            }\n        },\n        methods: {\n            submitForm(resetFormPassword) {\n              this.$refs[resetFormPassword].validate((valid) => {\n                if (valid) {\n                  alert('submit!');\n                  this.$router.push({path: '/resetPasswordDone'});\n                } else {\n                  console.log('error submit!!');\n                  return false;\n                }\n              });\n            },\n          resetForm(resetFormPassword) {\n            this.$refs[resetFormPassword].resetFields();\n          },\n\n          showFeedback ({suggestions, warning}) {\n            console.log('🙏', suggestions)\n            console.log('⚠', warning)\n          },\n          showScore (score) {\n            console.log('💯', score)\n          },\n        }\n    }\n</script>\n<style scoped>\n    #resetPassword {\n      max-width: 50%;\n      margin: 0 auto;\n    }\n    .title {\n        width: 50%;\n        margin: 0 auto;\n        text-align: center;\n    }\n    .content {\n        font-size: 16px;\n    }\n    .list {\n      font-size: 13px;\n    }\n\n</style>"],"sourceRoot":""}]);

